@import './variables.scss';
.App{
    footer{
        min-height: 500px;
        background: url('./../assets/cover.jpg') no-repeat center center fixed;
        margin-top: auto;
        background-size: cover;
        position: relative;
        .containerOpacity{
            background-color: rgba(25,30,36, 0.8);
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;   
            z-index: 1;
        }
    }
}